.product-hero {
	background-color: $dark;
	padding-top: 102px;
	min-height: 500px;

	@include media(tablet-993) {
		padding-top: 135px;
	}

	@include media(mobile) {
		min-height: 400px;
	}

	&__container {
		min-height: 500px;

		@include media(mobile) {
			min-height: 400px;
			width: 100%;
			max-width: 100%;
		}
	}

	&__inner {
		align-items: flex-start;
		padding-top: 13px;

		@include media(mobile) {
			flex-direction: column;
		}

		&-left {
			padding-left: 39px;

			@include media(tablet-993) {
				padding-left: 0;
				display: none;
			}
		}

		&-right {
			flex-direction: column;
			height: 100%;
			width: 76.5172%;
			padding-bottom: 18px;
			padding-top: 3px;
			position: relative;
			box-sizing: border-box;

			&::before {
				content: '';
				position: absolute;
				left: -1px;
				top: -64px;
				width: 1px;
				height: 550px;
				background-color: rgba(160, 171, 123, 0.12);

				@include media(tablet-993) {
					display: none;
				}
			}
			&::after {
				content: '';
				position: absolute;
				right: 13%;
				top: -64px;
				width: 1px;
				height: 550px;
				background-color: rgba(160, 171, 123, 0.12);

				@include media(tablet-993) {
					display: none;
				}
			}

			@include media(tablet-993) {
				padding-top: 48px;
				padding-left: 38px;
			}

			@include media(mobile) {
				flex-basis: 100%;
				width: 100%;
				overflow: auto;
				padding-left: 68px;
			}

			@include media(550) {
				padding-left: 0;
			}

			@include media(400) {
				padding-bottom: 23px;
			}
		}
	}

	&__title {
		font-family: $secondaryFont;
		font-weight: bold;
		font-size: 27px;
		line-height: 43px;

		@include media(mobile) {
			padding-left: 0;
			padding-right: 30px;
			font-size: 27px;
			line-height: 36px;
		}

		@include media(550) {
			padding-left: 32px;
			max-width: 430px;
		}

		@include media(400) {
			max-width: 300px;
			padding-right: 0;
		}
	}

	&__pagination {
		display: flex;
		padding-left: 16px;
		margin-top: 4px;

		@include media(mobile) {
		}

		@include media(550) {
			padding-right: 32px;
		}

		@include media(400) {
			padding-left: 28px;
			padding-right: 28px;
		}

		@include media(mobile) {
			margin-top: 0;
			width: 100%;
			overflow: auto;
		}

		li {
			position: relative;
			width: 125px;
			height: 77px;
			background-color: rgba(255, 255, 255, 0.02);
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;

			&:not(:last-child) {
				margin-right: 2px;
			}

			.timeline {
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				width: 0;
				height: 1px;
				background-color: $gold;
			}

			&.slick-active {
				.timeline {
					width: 100%;
					transition: width 3.2s linear;
				}
			}
		}
	}

	&__slider {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		
		&-wrapper {
			position: relative;
			min-height: 300px;
			width: 100%;
			z-index: 1;


			@include media(400) {
				min-height: 261px;
				margin-top: 30px;
			}
		}
	}

	&__slide {
		// position: absolute;
		// opacity: 0;
		min-height: 300px;
		height: 300px;
		width: 100%;

		@include media(mobile) {
			display: flex;
			align-items: center;
			height: auto;
		}

		@include media(400) {
			max-width: 353px;
			min-height: 220px;
			margin: 0 25px;
		}

		&.active {
			opacity: 1;
		}
	}

	&__button-prev {
		@include media(tablet-993) {
			display: none;
		}
	}
}
